function kazino() {
  const inputEnterRate = document.querySelector('#inputEnterRate'),
    btnEnterRate = document.querySelector('#btnEnterRate'),
    showCards = document.querySelector('#showCards'),
    gamePlayer = document.querySelector('#gamePlayer'),
    gamePC = document.querySelector('#gamePC'),
    gameResult = document.querySelector('#gameResult'),
    wrapBtn = document.querySelector('#wrapBtn'),
    btnContinue = document.querySelector('#btnContinue'),
    btnContinue2 = document.querySelector('#btnContinue2'),
    btnEndGame = document.querySelector('#btnEndGame'),
    btnEndGame2 = document.querySelector('#btnEndGame2'),
    btnMore = document.querySelector('#btnMore'),
    btnEnough = document.querySelector('#btnEnough'),
    infoValueBank = document.querySelector('#infoValueBank'),
    infoValuePlayer = document.querySelector('#infoValuePlayer'),
    inputHelpText = document.querySelector('#inputHelpText'),
    modal = document.querySelector('#modal'),
    modalContent = document.querySelector('#modalContent');


  /*泻芯谢芯写邪 36 泻邪*/
  const deck小ardsBasic = [
    { name: '蟹', color: '械薪泄', suit: '械胁邪', value: 11 },
    { name: '蟹', color: '泻邪薪泄', suit: '斜斜薪邪', value: 11 },
    { name: '蟹', color: '械薪泄', suit: '锌懈泻邪', value: 11 },
    { name: '蟹', color: '泻邪薪泄', suit: '械', value: 11 },

    { name: '泻芯芯谢', color: '械薪泄', suit: '械胁邪', value: 4 },
    { name: '泻芯芯谢', color: '泻邪薪泄', suit: '斜斜薪邪', value: 4 },
    { name: '泻芯芯谢', color: '械薪泄', suit: '锌懈泻邪', value: 4 },
    { name: '泻芯芯谢', color: '泻邪薪泄', suit: '械', value: 4 },

    { name: '写邪屑a', color: '械薪泄', suit: '械胁邪', value: 3 },
    { name: '写邪屑a', color: '泻邪薪泄', suit: '斜斜薪邪', value: 3 },
    { name: '写邪屑a', color: '械薪泄', suit: '锌懈泻邪', value: 3 },
    { name: '写邪屑a', color: '泻邪薪泄', suit: '械', value: 3 },

    { name: '胁邪谢械', color: '械薪泄', suit: '械胁邪', value: 2 },
    { name: '胁邪谢械', color: '泻邪薪泄', suit: '斜斜薪邪', value: 2 },
    { name: '胁邪谢械', color: '械薪泄', suit: '锌懈泻邪', value: 2 },
    { name: '胁邪谢械', color: '泻邪薪泄', suit: '械', value: 2 },

    { name: '械械泻邪', color: '械薪泄', suit: '械胁邪', value: 6 },
    { name: '械械泻邪', color: '泻邪薪泄', suit: '斜斜薪邪', value: 6 },
    { name: '械械泻邪', color: '械薪泄', suit: '锌懈泻邪', value: 6 },
    { name: '械械泻邪', color: '泻邪薪泄', suit: '械', value: 6 },

    { name: '械屑械泻邪', color: '械薪泄', suit: '械胁邪', value: 7 },
    { name: '械屑械泻邪', color: '泻邪薪泄', suit: '斜斜薪邪', value: 7 },
    { name: '械屑械泻邪', color: '械薪泄', suit: '锌懈泻邪', value: 7 },
    { name: '械屑械泻邪', color: '泻邪薪泄', suit: '械', value: 7 },

    { name: '胁芯屑械泻邪', color: '械薪泄', suit: '械胁邪', value: 8 },
    { name: '胁芯屑械泻邪', color: '泻邪薪泄', suit: '斜斜薪邪', value: 8 },
    { name: '胁芯屑械泻邪', color: '械薪泄', suit: '锌懈泻邪', value: 8 },
    { name: '胁芯屑械泻邪', color: '泻邪薪泄', suit: '械', value: 8 },

    { name: '写械胁泻邪', color: '械薪泄', suit: '械胁邪', value: 9 },
    { name: '写械胁泻邪', color: '泻邪薪泄', suit: '斜斜薪邪', value: 9 },
    { name: '写械胁泻邪', color: '械薪泄', suit: '锌懈泻邪', value: 9 },
    { name: '写械胁泻邪', color: '泻邪薪泄', suit: '械', value: 9 },

    { name: '写械泻邪', color: '械薪泄', suit: '械胁邪', value: 10 },
    { name: '写械泻邪', color: '泻邪薪泄', suit: '斜斜薪邪', value: 10 },
    { name: '写械泻邪', color: '械薪泄', suit: '锌懈泻邪', value: 10 },
    { name: '写械泻邪', color: '泻邪薪泄', suit: '械', value: 10 },
  ];

  let rate,
    scorePlayer = 0,
    scorePC = 0,
    moneyBank = 0,
    moneyPlayer = 0;

  function updMoney(bank, player) {
    moneyBank = moneyBank + bank;
    moneyPlayer = moneyPlayer + player;
    infoValueBank.innerHTML = moneyBank; // 胁胁芯写 蟹薪邪械薪懈 斜邪薪泻邪 薪邪 邪薪懈
    infoValuePlayer.innerHTML = moneyPlayer; // 胁胁芯写 蟹薪邪械薪懈 械邪 懈谐芯泻邪 薪邪 邪薪懈
  }
  updMoney(28500, 25000); // 蟹邪锌泻 薪泻懈懈  薪邪邪谢薪屑懈 蟹薪邪械薪懈屑懈 斜邪薪泻邪 懈 械邪 懈谐芯泻邪

  /*芯斜薪芯胁懈 泻芯谢芯写*/
  let deck小ards;
  function newDeckCards() {
    deck小ards = JSON.parse(JSON.stringify(deck小ardsBasic)); // copyObj 斜写械 邪薪懈 泻芯锌懈 mainObj
  }

  /*===================*/
  /*===锌懈薪懈屑邪 邪胁泻 芯 懈谐芯泻邪===*/
  inputEnterRate.addEventListener('focus', () => {
    inputEnterRate.value = ""; // 懈 锌芯谢械薪懈懈 芯泻邪 写邪谢械屑 芯芯斜械薪懈械 芯斜 芯懈斜泻械, .泻. 锌芯谢蟹芯胁邪械谢 芯械 胁胁械懈 写邪薪薪械 蟹邪薪芯胁芯
    inputHelpText.innerHTML = '小屑械谢械械!';
    inputHelpText.classList.remove('field-text__help-text--error');
  });

  /* 薪邪卸邪谢 "写械谢邪 邪胁泻"*/
  btnEnterRate.addEventListener('click', () => {
    newDeckCards(); // 芯斜薪芯胁懈 泻芯谢芯写 泻邪

    rate = document.querySelector('#inputEnterRate').value;

    /*锌芯胁械泻邪 泻芯械泻薪芯懈 胁胁械写械薪薪 写邪薪薪 */
    const reg = /\D/ig; // 锌邪械 械谐谢薪芯谐芯 胁邪卸械薪懈; 懈械屑 -懈
    if (reg.test(rate)) {
      inputHelpText.innerHTML = '芯芯卸械, 胁 锌械械斜邪谢懈 邪谢泻芯谐芯谢... 小泻芯薪械薪懈泄械  -))';
      inputHelpText.classList.add('field-text__help-text--error');
    }
    else if (moneyBank == 0) {
      modal.style.display = 'block';
      modalContent.style.display = 'block';
    }
    else if (rate <= 0) {
      inputHelpText.innerHTML = '小写械谢邪泄械 邪胁泻...';
      inputHelpText.classList.add('field-text__help-text--error');
    }
    else if (rate > moneyPlayer) {
      inputHelpText.innerHTML = '校 邪 薪械 芯谢泻芯 写械薪械谐!';
      inputHelpText.classList.add('field-text__help-text--error');
    }
    else if (rate > moneyBank) {
      inputHelpText.innerHTML = '  薪械 芯谢泻芯 写械薪械谐!';
      inputHelpText.classList.add('field-text__help-text--error');
    }
    else if (rate > 0 && rate < 100) {
      inputHelpText.innerHTML = '小 邪泻芯泄 邪胁泻芯泄 懈谐邪 胁 锌芯写胁芯芯薪械, 邪 薪械  薪邪!';
      inputHelpText.classList.add('field-text__help-text--error');
    }
    else if (rate >= 100 && rate < 1000) {
      inputHelpText.innerHTML = ' 谢械写懈泄 邪蟹 斜写械 械懈械谢薪械械!';
      inputHelpText.classList.remove('field-text__help-text--error');
      rate = +rate; //锌械芯斜邪蟹芯胁邪薪懈械 芯泻懈 胁 懈谢芯
      showCards.style.display = 'block'; // 锌芯泻邪蟹邪 胁懈蟹邪谢懈蟹邪懈 邪蟹写邪懈 泻邪
      gamePlayer.style.display = 'block'; // // 锌芯泻邪蟹邪 斜谢芯泻 懈谐
      wrapBtn.style.display = 'flex'; // // 锌芯泻邪蟹邪 斜谢芯泻 泻薪芯锌芯泻 1
      updScorePlayer(); // 懈 写械谢邪 锌械胁 胁写邪 泻邪
    }
    else {
      inputHelpText.innerHTML = '啸芯芯邪 邪胁泻邪!';
      inputHelpText.classList.remove('field-text__help-text--error');
      rate = +rate; //锌械芯斜邪蟹芯胁邪薪懈械 芯泻懈 胁 懈谢芯
      showCards.style.display = 'block'; // 锌芯泻邪蟹邪 胁懈蟹邪谢懈蟹邪懈 邪蟹写邪懈 泻邪
      gamePlayer.style.display = 'block'; // // 锌芯泻邪蟹邪 斜谢芯泻 懈谐
      wrapBtn.style.display = 'flex'; // // 锌芯泻邪蟹邪 斜谢芯泻 泻薪芯锌芯泻 1
      updScorePlayer(); //懈 写械谢邪 锌械胁 胁写邪 泻邪
    }
  });
  /*===================*/

  /*薪泻懈 谢邪泄薪芯谐芯 懈谢邪*/
  function getRandomInt(max) {
    return Math.floor(Math.random() * Math.floor(max));
  }

  /*薪泻懈 芯泻胁邪械 modal2 谢邪泄薪屑 芯斜邪蟹芯屑*/
  function getRandomModal2() {
    const rndModal2 = getRandomInt(1);

    if (rndModal2 == 0) {
      modal2();
    }
  }

  /*薪泻懈 胁写邪械 谢邪泄薪 泻邪 懈蟹 泻芯谢芯写, 懈 屑械薪邪械 泻芯谢-胁芯 泻邪 胁 泻芯谢芯写械*/
  let arr = [];
  function getRandomCard(selector) {
    const rnd = getRandomInt(deck小ards.length); // 蟹邪锌懈 懈薪写械泻 谢邪泄薪芯泄 泻邪
    const rndCardValue = deck小ards[rnd].value; // 蟹邪锌懈邪  蟹薪邪械薪懈械 谢邪泄薪芯泄 泻邪
    const newCard = document.createElement('p');
    newCard.classList.add('distribution__card');
    newCard.innerHTML = `${deck小ards[rnd].name} = ${deck小ards[rnd].value} 芯泻芯胁`;
    document.querySelector(selector).append(newCard);  // 写芯斜邪胁谢械薪懈械 薪邪 邪薪懈 懈薪芯 芯 胁锌邪胁械泄 泻邪械

    /*蟹邪锌懈胁邪 泻邪 胁 屑邪懈胁*/
    //屑邪懈胁 胁锌邪胁 泻邪懈胁 薪卸械薪 写谢 芯谐芯, 芯-斜 锌芯谢懈 锌懈 懈谐械  锌谢邪胁薪泄 胁胁芯写 泻邪 薪邪 邪薪懈
    let obj = {};
    obj.name = deck小ards[rnd].name;
    obj.value = deck小ards[rnd].value;
    arr.push(obj);
    deck小ards.splice(rnd, 1); // // 薪邪懈薪邪  锌芯蟹懈懈懈 rnd, 写邪谢懈 1 谢械屑械薪

    return rndCardValue;
  }

  /*薪泻懈 蟹邪锌泻邪械 胁写邪 泻邪 懈蟹 泻芯谢芯写 懈 懈邪械 芯泻懈 */
  function updScorePlayer() {
    const newCardValue = getRandomCard('#distributionPlayer'); // 胁 锌械械屑械薪薪 蟹邪锌懈邪 芯, 芯 胁械薪谢邪 薪泻懈 谢邪泄薪芯泄 泻邪,
    scorePlayer += newCardValue;
    document.querySelector('#totalScorePlayer').innerHTML = `${scorePlayer}`;
    arr.splice(0, 1); // 泻芯谐写邪 懈谐邪械 懈谐芯泻, 写邪谢械屑 蟹邪锌懈 芯 胁锌邪胁械泄 泻邪械 懈蟹 屑邪懈胁邪
  }

  /*薪泻懈 蟹邪锌泻邪械 胁写邪 泻邪 懈蟹 泻芯谢芯写 懈 懈邪械 芯泻懈 */
  function updScorePC() {
    const newCardValue = getRandomCard('#distributionPC'); // 胁 锌械械屑械薪薪 蟹邪锌懈邪 芯, 芯 胁械薪谢邪 薪泻懈 谢邪泄薪芯泄 泻邪,
    scorePC += newCardValue;
    document.querySelector('#totalScorePC').innerHTML = `${scorePC}`;
  }

  /*薪泻懈 锌芯屑械邪械 芯芯斜械薪懈械 ! 薪邪 邪薪懈*/
  function overdo(elem) {
    const excess = document.createElement('p');
    excess.classList.add('excess');
    excess.innerHTML = '!';
    document.querySelector(elem).append(excess);
  }

  /*薪泻懈 写邪谢械 芯芯斜械薪懈械 ! 芯 邪薪懈*/
  function overdoRemove(elem) {
    document.querySelector(elem).remove();
  }

  /*===================*/
  /*斜谢芯泻 泻薪芯锌芯泻 1, ,  懈谐芯泻 薪邪卸邪谢 "械 泻邪"*/
  btnMore.addEventListener('click', () => {
    updScorePlayer();

    /*写械泄胁懈, 械谢懈 谢懈谢 "锌械械斜芯"  */
    if (scorePlayer >= 22) {
      updMoney(rate, (-rate)); // 薪 邪胁泻 芯 械邪 懈谐芯泻邪 懈 写芯斜邪胁懈 邪胁泻 胁 斜邪薪泻
      overdo('#textScorePlayer');// 写芯斜邪胁懈 薪邪 邪薪懈 械泻 "!"

      /*斜邪/锌芯泻邪蟹邪 泻薪芯锌泻懈*/
      btnMore.style.display = 'none'; // 械械 泻邪!
      btnEnough.style.display = 'none'; // 胁邪懈...
      btnContinue.style.display = 'inline-block'; //锌芯写芯谢卸懈 懈谐-1
      btnEndGame.style.display = 'inline-block'; // 薪械-1

      scorePlayer = 0; //芯斜薪谢懈 芯泻懈 懈谐芯泻邪
    }
  });

  /*斜谢芯泻 泻薪芯锌芯泻 1, , 懈谐芯泻 薪邪卸邪谢 "胁邪懈", 械锌械 懈谐邪械 */
  /**===OK===*/
  btnEnough.addEventListener('click', () => {
    wrapBtn.style.display = 'none'; // 泻 斜谢芯泻 泻薪芯锌芯泻 1
    gamePC.style.display = 'block'; // 锌芯泻邪蟹邪 斜谢芯泻 懈谐 

    /* 写芯谢卸械薪 胁写邪胁邪 械斜械 泻邪, 锌芯泻邪 薪械 芯邪薪芯胁懈, 懈谢懈 薪械 谢懈 "锌械械斜芯!"*/
    while (scorePC <= 16) {
      updScorePC();
    }

    /*  薪邪斜邪谢 写芯邪芯薪芯 泻邪, 胁薪械屑, 泻芯 胁懈谐邪谢*/
    if (scorePC >= 22) {
      const gameResultText = document.createElement('p');
      gameResultText.classList.add('game-result__content');
      gameResultText.innerHTML = ` 胁懈谐邪谢懈 ${rate} 写芯谢谢邪芯胁`;
      gameResult.append(gameResultText);

      updMoney((-rate), rate); //  薪 邪胁泻 懈蟹 斜邪薪泻邪 懈 写芯斜邪胁懈 邪胁泻 薪邪 械 
      overdo('#textScorePC');  // 写芯斜邪胁懈 薪邪 邪薪懈 械泻 "!"
      scorePC = 0; //芯斜薪谢懈 芯泻懈 
    }
    else if (scorePlayer >= scorePC) {
      /*胁懈谐邪谢 */
      const gameResultText = document.createElement('p');
      gameResultText.classList.add('game-result__content');
      gameResultText.innerHTML = ` 胁懈谐邪谢懈 ${rate}`;
      gameResult.append(gameResultText);
      updMoney((-rate), rate);
    } else {
      /*胁懈谐邪谢 */
      const gameResultText = document.createElement('p');
      gameResultText.classList.add('game-result__content');
      gameResultText.innerHTML = ` 锌芯懈谐邪谢懈 ${rate}`;
      gameResult.append(gameResultText);
      updMoney(rate, (-rate));
    }

    btnContinue2.style.display = 'inline-block'; //泻薪芯锌泻邪 "锌芯写芯谢卸懈 懈谐-2"; - 斜谢芯泻 泻薪芯锌芯泻 #2
    btnEndGame2.style.display = 'inline-block'; //泻薪芯锌泻邪 '"薪械-2", 薪械 锌芯写芯谢卸邪; - 斜谢芯泻 泻薪芯锌芯泻 #2

  });
  /*===================*/

  /*斜谢芯泻 泻薪芯锌芯泻 1, !, 薪邪卸邪懈械 薪邪 "锌芯写芯谢卸懈 懈谐-1"*/
  /**===OK===*/
  btnContinue.addEventListener('click', () => {
    /* 写邪谢懈 胁锌邪胁懈械 泻邪*/
    const distCard = document.querySelectorAll('.distribution__card');
    distCard.forEach((item) => {
      item.remove();
    });

    gamePlayer.querySelector('.excess').remove(); // 写邪谢懈 谢械屑械薪  械泻芯屑 

    /*斜邪/锌芯泻邪蟹邪 泻薪芯锌泻懈*/
    btnMore.style.display = 'inline-block'; // 械械 泻邪!
    btnEnough.style.display = 'inline-block'; // 胁邪懈...
    btnContinue.style.display = 'none'; // 锌芯写芯谢卸懈 懈谐-1
    btnEndGame.style.display = 'none'; // 薪械-1

    showCards.style.display = 'none'; // 锌芯泻邪蟹邪 斜谢芯泻 锌懈屑 邪胁泻懈
    gamePlayer.style.display = 'none'; // 泻 斜谢芯泻 懈谐 
    getRandomModal2(); // 懈 蟹邪锌懈 谐械薪械邪芯 写谢 modal2
  });

  /*斜谢芯泻 泻薪芯锌芯泻 1, !, 谐芯泻 薪邪卸邪谢 "薪械", 薪械 锌芯写芯谢卸邪 懈谐*/
  btnEndGame.addEventListener('click', () => {
    modal.style.display = 'block';
    modalContent.style.display = 'block';
  });

  /*斜谢芯泻 泻薪芯锌芯泻 2 懈谐芯泻 薪邪卸邪谢 "锌芯写芯谢卸懈 懈谐-2*/
  btnContinue2.addEventListener('click', () => {
    scorePlayer = 0; //芯斜薪谢懈 芯泻懈 懈谐芯泻邪
    scorePC = 0; //芯斜薪谢懈 芯泻懈 

    /*写邪谢懈 胁锌邪胁懈械 泻邪*/
    const distCard = document.querySelectorAll('.distribution__card');
    distCard.forEach((item) => {
      item.remove();
    });

    /* 械谢懈 械 芯芯斜械薪懈械 , 写邪谢懈 械谐芯 */
    if (gamePC.querySelector('.excess')) {
      gamePC.querySelector('.excess').remove();
    }

    /* 械谢懈 械 芯芯斜械薪懈械 蝎 /蝎 写邪谢懈 械谐芯 */
    if (gamePC.querySelector('.game-result__content')) {
      gamePC.querySelector('.game-result__content').remove();
    }

    gamePlayer.style.display = 'none'; /* 写邪谢懈 斜谢芯泻 懈谐 */
    gamePC.style.display = 'none'; /* 写邪谢懈 斜谢芯泻懈 懈谐 */
    showCards.style.display = 'none';// 锌芯泻邪蟹邪 斜谢芯泻 锌懈屑 邪胁泻懈
    getRandomModal2();// 懈 蟹邪锌懈 谐械薪械邪芯 写谢 modal2
  });

  /*斜谢芯泻 泻薪芯锌芯泻 2 懈谐芯泻 薪邪卸邪谢 "薪械", 薪械 锌芯写芯谢卸邪 懈谐-2*/
  btnEndGame2.addEventListener('click', () => {
    modal.style.display = 'block';
    modalContent.style.display = 'block';
  });


  /**=======================*/
  /**===modal2==============*/
  function modal2() {
    const modal2 = document.querySelector('#modal2'),
      modal2BtnYes1 = document.querySelector('#modal2BtnYes1'), // 写邪, 写邪 胁蟹泻
      modal2BtnNo1 = document.querySelector('#modal2BtnNo1'), //薪械, 薪械 写邪胁邪 胁蟹泻
      wrapBribeYes = document.querySelector('#wrapBribeYes'), // 芯斜械泻邪 写谢 斜谢芯泻 胁蟹泻邪
      inputEnterBribe = document.querySelector('#inputEnterBribe'), // 锌芯谢械 胁胁芯写邪 屑屑
      modal2BtnBribe = document.querySelector('#modal2BtnBribe'), // 锌芯写胁械写懈 屑屑
      modal2Btn小ontinue = document.querySelectorAll('.btn--continue-modal2'), //泻芯谢谢械泻懈 泻薪芯锌芯泻
      modal2TextOk = document.querySelector('#modal2TextOk'), // 写芯胁芯谢械薪
      modal2TextNo = document.querySelector('#modal2TextNo'), // 薪械 写芯胁芯谢械薪
      wrapBribeNo = document.querySelector('#wrapBribeNo'),
      modal2BtnYes2 = document.querySelector('#modal2BtnYes2'), // 卸邪谢芯斜邪 胁 写 - 
      modal2BtnNo2 = document.querySelector('#modal2BtnNo2'), //卸邪谢芯斜邪 胁 写 - 孝
      notGuilty = document.querySelector('#notGuilty'), // 斜谢芯泻  胁懈薪芯胁械薪
      compensation = document.querySelector('#compensation'),
      payFine = document.querySelector('#payFine'),
      courtCosts = document.querySelector('#courtCosts'),
      guilty = document.querySelector('#guilty'); // 斜谢芯泻 懈薪芯胁械薪

    console.log(modal2Btn小ontinue);


    /**薪泻懈 锌懈胁芯写懈 modal2 胁 锌械胁芯薪邪邪谢薪芯械 芯芯薪懈械*/
    function makeModalStartState() {
      modal2.classList.add('modal2--show'),
        wrapBribeYes.style.display = 'none',
        modal2TextOk.style.display = 'none',
        modal2TextNo.style.display = 'none',
        modal2BtnYes1.style.display = 'inline-block',
        modal2BtnYes1.classList.remove('btn--disabled'),
        modal2BtnNo1.style.display = 'inline-block',
        modal2BtnNo1.classList.remove('btn--disabled'),
        inputEnterBribe.value = "",
        modal2BtnBribe.style.display = 'none',
        wrapBribeNo.style.display = 'none',
        modal2BtnYes2.style.display = 'inline-block',
        modal2BtnYes2.classList.remove('btn--disabled'),
        modal2BtnNo2.style.display = 'inline-block',
        modal2BtnNo2.classList.remove('btn--disabled'),
        notGuilty.style.display = 'none',
        compensation.innerHTML = "",
        guilty.style.display = 'none',
        payFine.innerHTML = "",
        courtCosts.innerHTML = "",

        modal2Btn小ontinue.forEach(item => {
          item.style.display = 'none';
        });
    }
    makeModalStartState();

    /**薪泻懈 谢邪泄薪芯械 懈谢芯 胁 写懈邪锌邪蟹芯薪械*/
    function getRandomRange(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min)) + min; //邪泻懈屑屑 薪械 胁泻谢邪械, 屑懈薪懈屑屑 胁泻谢邪械
    }

    /**薪泻懈 小校 小携蝎啸*/
    function jury小ourt() {
      const rnd1 = getRandomRange(0, 2); // 蟹邪锌懈 谐械薪械邪芯 谢邪泄薪芯谐芯 懈谢邪

      if (rnd1 == 0) {
        /* 薪械 胁懈薪芯胁械薪 */
        const rnd2 = getRandomRange(1000, 3000);

        compensation.innerHTML = rnd2; // 泻芯屑锌械薪邪懈
        notGuilty.style.display = 'block';
        moneyPlayer += rnd2; //写芯斜邪胁懈 屑屑 泻芯屑锌械薪邪懈懈 泻 写械薪谐邪屑 懈谐芯泻邪
        infoValuePlayer.innerHTML = moneyPlayer; // 胁胁芯写 蟹薪邪械薪懈 械邪 懈谐芯泻邪 薪邪 邪薪懈
        modal2Btn小ontinue[1].style.display = 'inline-block';
      } else {
        /* 胁懈薪芯胁械薪 */
        const rnd3 = getRandomRange(200, 1000),
          rnd4 = getRandomRange(100, 750);

        payFine.innerHTML = rnd3; //邪
        courtCosts.innerHTML = rnd4; // 写械斜薪械 懈蟹写械卸泻懈
        guilty.style.display = 'block';
        moneyPlayer -= rnd3;//芯薪 屑屑 邪邪 芯 写械薪械谐 懈谐芯泻邪
        moneyPlayer -= rnd4;//芯薪 屑屑 写.懈蟹写械卸械泻 芯 写械薪械谐 懈谐芯泻邪
        infoValuePlayer.innerHTML = moneyPlayer; // 胁胁芯写 蟹薪邪械薪懈 械邪 懈谐芯泻邪 薪邪 邪薪懈
        modal2Btn小ontinue[2].style.display = 'inline-block';
      }
    }

    /**芯斜邪斜芯懈泻 泻邪卸写芯屑 谢械屑械薪 泻芯谢谢械泻懈懈*/
    modal2Btn小ontinue.forEach((item) => {
      item.addEventListener('click', () => {
        modal2.classList.remove('modal2--show');
      });
    });

    /** ===  写邪屑 胁蟹泻 ===*/
    modal2BtnYes1.addEventListener('click', () => {
      modal2BtnYes1.classList.add('btn--disabled');
      modal2BtnNo1.style.display = 'none';
      wrapBribeYes.style.display = 'block';
    }, { once: true });

    inputEnterBribe.addEventListener('focus', () => {
      inputEnterBribe.value = "";
      modal2BtnBribe.style.display = 'inline-block';
    });

    modal2BtnBribe.addEventListener('click', (event) => {
      const bribe = inputEnterBribe.value;
      const reg = /\D/ig; // 锌邪械 械谐谢薪芯谐芯 胁邪卸械薪懈; 懈械屑 -懈
      if (reg.test(bribe) || bribe < 500) {
        modal2BtnBribe.style.display = 'none';
        modal2TextNo.style.display = 'block'; //楔械懈 薪械 写芯胁芯谢械薪
        modal2Btn小ontinue[0].style.display = 'inline-block';
      } else {
        modal2BtnBribe.style.display = 'none';
        modal2TextOk.style.display = 'block'; //楔械懈 薪械 写芯胁芯谢械薪
        modal2Btn小ontinue[0].style.display = 'inline-block';
      }

      moneyPlayer -= bribe; //芯薪 屑屑 胁蟹泻懈 芯 写械薪械谐 懈谐芯泻邪
      infoValuePlayer.innerHTML = moneyPlayer; // 胁胁芯写 蟹薪邪械薪懈 械邪 懈谐芯泻邪 薪邪 邪薪懈
    }, { once: true });


    /** ===   写邪屑 胁蟹泻 ===*/
    modal2BtnNo1.addEventListener('click', () => {
      modal2BtnYes1.style.display = 'none';
      modal2BtnNo1.classList.add('btn--disabled');
      wrapBribeNo.style.display = 'block';
    }, { once: true });

    /** ===  锌芯写邪屑 卸邪谢芯斜 胁 写*/
    modal2BtnYes2.addEventListener('click', () => {
      modal2BtnYes2.classList.add('btn--disabled');
      modal2BtnNo2.style.display = 'none';
      jury小ourt();
      //modal2Btn小ontinue[1].style.display = 'inline-block';
    }, { once: true });

    /** ===  薪械-锌芯写邪屑 卸邪谢芯斜 胁 写*/
    modal2BtnNo2.addEventListener('click', () => {
      modal2BtnYes2.style.display = 'none';
      modal2BtnNo2.classList.add('btn--disabled');
      jury小ourt();
      //modal2Btn小ontinue[2].style.display = 'inline-block';
    }, { once: true });
  }
}

export default kazino;
